<template lang="pug">
  div(class="register-root")
    div
      div(class="logo-font")
      div
        form(@submit.prevent="register")
          div
            div
              input(type="email" class="input-default" placeholder="Email" v-model="email")
            div
              input(type="text" class="input-default" placeholder="玩家名稱" ref="username" v-model="username")
            div
              input(type="password" class="input-default" placeholder="密碼" v-model="password")
              input(type="password" class="input-default" placeholder="密碼確認" v-model="checkpassword")
            p 密碼請使用多於 6 個字的英文字母或數字
            button(type="submit" class="button-default") 註冊
            p 點擊註冊的同時，代表您同意 Kyronus <br>的
              a(href="#") 資料使用政策與使用條款
        div
          div
            router-link(tag="span" to="/login") 已經有帳號了？點我登入
          span(id="usernameCopy" ref="usernameCopy")
      error-message(v-if="isError" :errorMsg="errorMsg")
</template>

<script>
import ErrorMessage from '@/components/ErrorMessage.vue'
import { mapMutations } from 'vuex'

export default {
  components: {
    ErrorMessage
  },
  data () {
    return {
      email: '',
      username: '',
      password: '',
      checkpassword: '',
      isError: false,
      errorMsg: '',
      message: {
        400: '輸入含不合法字元',
        406: '信箱已註冊過，請選擇新的信箱'
      }
    }
  },
  methods: {
    ...mapMutations('user', ['SET_ISLOADING']),
    async register () {
      try {
        if (this.textSize(this.$refs.username.value) > 84) {
          this.displayError('玩家名稱過長，請縮減字數')
          return
        }
        if (this.email === '') {
          this.displayError('請輸入信箱')
          return
        }
        if (this.username === '') {
          this.displayError('請輸入使用者名稱')
          return
        }
        if (this.password === '') {
          this.displayError('請輸入密碼')
          return
        }
        if (this.password.length < 6) {
          this.displayError('密碼請設置至少6個字母或數字')
          return
        }
        if (this.password !== this.checkpassword) {
          this.displayError('密碼與確認密碼不一致！')
          return
        }
        await this.$store.dispatch('user/register', {
          email: this.email,
          password: this.password,
          username: this.username
        })
        this.$router.push('/home')
      } catch (error) {
        console.log(error.response)
        console.log('進入catch')
        this.displayError(this.message[error.response.status])
      }
    },
    textSize (text) {
      let resultWidth
      if (typeof this.$refs.usernameCopy.textContent !== 'undefined') {
        this.$refs.usernameCopy.textContent = text
      } else {
        this.$refs.usernameCopy.innerText = text
      }
      console.log(window.getComputedStyle(this.$refs.usernameCopy).width)
      resultWidth = parseFloat(window.getComputedStyle(this.$refs.usernameCopy).width)
      return resultWidth
    },
    displayError (msg) {
      let self = this
      self.errorMsg = msg
      self.isError = true
      setTimeout(() => {
        self.isError = false
      }, 2000)
    }
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";

.register-root {
  width: 100%;
  height: 100%;
  background-color: black;
  position: absolute;
  z-index: 10;
  > div:nth-child(1) {
    width: 100%;
    height: 100%;
    background-image: url("~@/assets/image/background/bg.png");
    background-position: center;
    background-size: cover;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 154px 513px;
    > div:nth-child(2) {
      box-sizing: border-box;
      justify-self: center;
      width: 324px;
      border: 1px solid map-get($colors, white);
      border-bottom: none;
      border-top-left-radius: 7.5px;
      border-top-right-radius: 7.5px;
      background-color: rgba($color: map-get($colors, black), $alpha: 0.7);
      box-shadow: map-get($shadow, secondary-4);
      padding: 0 24px;

      display: grid;
      grid-template-rows: 349px 1fr;
      grid-row-gap: 93px;
      > form:nth-child(1) {
        > div {
          display: grid;
          grid-template-rows: 50px 50px 13px 50px 13px 100px 12px 12px 50px 32px 11px 29px;
          grid-template-areas:
            "."
            "email"
            "."
            "username"
            "."
            "password"
            "."
            "hint"
            "."
            "register"
            "."
            "terms";
          > div:nth-child(1) {
            grid-area: email;
          }
          > div:nth-child(2) {
            grid-area: username;
          }
          > div:nth-child(3) {
            grid-area: password;
            > input {
              border-radius: 0px;
              height: 50%;
              &:nth-child(1) {
                border-top-left-radius: 7.5px;
                border-top-right-radius: 7.5px;
              }
              &:nth-child(2) {
                border-bottom-left-radius: 7.5px;
                border-bottom-right-radius: 7.5px;
              }
            }
          }
          > p:nth-child(4) {
            grid-area: hint;
            font-size: 12px;
            text-shadow: map-get($shadow, secondary-4);
          }
          > button:nth-child(5) {
            grid-area: register;
            justify-self: center;
            width: 116px;
          }
          > p:nth-child(6) {
            grid-area: terms;
            font-size: 10px;
            color: rgba($color: map-get($colors, white), $alpha: 0.5);
            a:link,
            a:visited {
              color: rgba($color: map-get($colors, white), $alpha: 0.5);
            }
          }
        }
      }
      > div:nth-child(2) {
        justify-self: center;
        width: 132px;
        > div {
          border-bottom: 1px solid map-get($colors, white);
          > span {
            display: block;
            font-size: 11px;
            line-height: 11px;
            padding-bottom: 4px;
            text-shadow: map-get($shadow, secondary-4);
          }
        }
        #usernameCopy {
          font-size: 14px;
          visibility: hidden;
          display: inline-block;
        }
      }
    }
  }
}
</style>
